@import url(https://fonts.googleapis.com/css2?family=Sora&family=Satisfy&display=swap);
* {
  font-family: 'Sora', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none; /* no underline */
}

body {
  background: url(/static/media/background_alps.bcd59a26.jpeg) no-repeat center center fixed;
  background-size: cover;
  background-color: rgba(255,255,255,0.8);
  background-blend-mode: lighten;
}
